import React, { useState, useEffect } from "react";
import axios from "axios";
import "../styles/User_managment.css";
import axiosInstance from "../../api/api";


function UserManagement() {
    const [menuItems, setMenuItems] = useState([]); // Lu danh sch tt c ngi dng
    const [filteredItems, setFilteredItems] = useState([]); // Lu danh sch ngi dng  lc
    const [searchTerm, setSearchTerm] = useState(""); // Lu t kha tm kim

    // Fetch tt c ngi dng t backend
    useEffect(() => {
        const fetchMenuItems = async () => {
            try {
                const token = localStorage.getItem("authToken"); // Retrieve the token
                const response = await axiosInstance.get("/api/admin/users", {
                    headers: {
                        Authorization: `Bearer ${token}`, // Pass the token
                    },
                });
                setMenuItems(response.data);
                setFilteredItems(response.data); // Mc nh hin th tt c ngi dng
            } catch (error) {
                console.error("Error fetching users:", error);
            }
        };

        fetchMenuItems();
    }, []);

    // Hm x l tm kim
    const handleSearch = (e) => {
        const value = e.target.value.toLowerCase();
        setSearchTerm(value);

        // Lc danh sch theo t kha
        const filtered = menuItems.filter(
            (item) =>
                item.user_name.toLowerCase().includes(value) ||
                item.address.toLowerCase().includes(value) ||
                item.email.toLowerCase().includes(value) ||
                item.phone.includes(value)
        );
        setFilteredItems(filtered);
    };

    return (
        <div>
            {/* Thanh tm kim */}
            <nav className="navbar bg-body-tertiary">
                <div className="container-fluid">
                    <form className="d-flex" role="search">
                        <input
                            className="form-control me-2"
                            type="search"
                            placeholder="Tm kim theo tn, a ch, email hoc s in thoi"
                            aria-label="Search"
                            value={searchTerm}
                            onChange={handleSearch} // Gi hm x l tm kim
                            style={{width: "500px", height: "40px"}}
                        />
                        {/* <button
                            className="btn btn-outline-success"
                            type="button"
                            onClick={() => console.log("Search triggered!")}
                            style={{ height: "40px" }} // ng b chiu cao
                        >
                            Search
                        </button> */}
                    </form>
                </div>
            </nav>

            {/* Bng hin th danh sch ngi dng */}
            <div className="card-body">
                <h5 className="card-title">Qun l ngi dng</h5>
                <table className="mb-0 table table-bordered">
                    <thead>
                        <tr>
                            <th>STT</th>
                            <th>Tn ngi dng</th>
                            <th>a ch</th>
                            <th>Email</th>
                            <th>S T</th>
                        </tr>
                    </thead>
                    <tbody>
                        {filteredItems.map((item, index) => (
                            <tr key={item.user_id}>
                                <th scope="row">{index + 1}</th>
                                <td>{item.user_name}</td>
                                <td>{item.address}</td>
                                <td>{item.email}</td>
                                <td>{item.phone}</td>
                            </tr>
                        ))}
                        {filteredItems.length === 0 && (
                            <tr>
                                <td colSpan="5" style={{ textAlign: "center" }}>
                                    Khng tm thy kt qu ph hp
                                </td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    );
}

export default UserManagement;
